import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path:'/app1',
    name:'app1',
    component: ()=> import('@/views/App1/App1.vue')
  },
  {
    path:'/app2',
    name:'app2',
    component: ()=> import('@/views/App2/App2.vue')
  },
]

const router = new VueRouter({
  mode:'history',
  routes,
})

export default router
